'use client'
import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';
import Form from 'react-bootstrap/Form';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';

// Ganti nama fungsi menjadi PascalCase (best practice React)
function MainNavbar() {
    return(
        <Navbar expand= "lg" className="bg-body-tertiary" >
            <Container fluid>
                {/* DIPERBARUI: Brand Name */}
                <Navbar.Brand href="#">Naisya Yuen</Navbar.Brand>
                <Navbar.Toggle aria-controls="navbarScroll" />
                <Navbar.Collapse id="navbarScroll">
                    
                    {/* DIPERBARUI: Semua link dipindahkan ke dalam <Nav> agar bisa scroll */}
                    <Nav
                        className="me-auto my-2 my-lg-0"
                        style={{ maxHeight: '150px' }} // Anda bisa sesuaikan tingginya
                        navbarScroll
                    >
                        <Nav.Link href="#home">Home</Nav.Link>
                        <Nav.Link href="#about">About</Nav.Link>
                        <Nav.Link href="#portfolio">Portfolio</Nav.Link>
                        <Nav.Link href="#services">Services</Nav.Link>
                        
                        {/* Contoh Dropdown jika masih diperlukan */}
                        <NavDropdown title="Projects" id="navbarScrollingDropdown">
                            <NavDropdown.Item href="#project1">Project 1</NavDropdown.Item>
                            <NavDropdown.Item href="#project2">Project 2</NavDropdown.Item>
                            <NavDropdown.Divider />
                            <NavDropdown.Item href="#project3">
                                Something else here
                            </NavDropdown.Item>
                        </NavDropdown>
                        
                        {/* Link ini bisa dihapus jika tidak perlu */}
                        <Nav.Link href="#" disabled>
                            Disabled
                        </Nav.Link>
                    </Nav>

                    {/* Memindahkan Form dan Tombol Kontak ke sisi kanan */}
                    <Form className="d-flex me-2 mb-2 mb-lg-0">
                    <Form.Control
                        type="search"
                        placeholder="Search"
                        className="me-2"
                        aria-label="Search"
                    />
                    <Button variant="outline-success">Search</Button>
                    </Form>
                    <Button href="#contact" variant="outline-primary">Contact Me</Button>
                    
                </Navbar.Collapse>
            </Container>
        </Navbar>
    )
}

export default MainNavbar;