"use client"
import Carousel from 'react-bootstrap/Carousel';

interface Slide {
    title: string;
    subtitle: string;
    imageUrl: string;
}

const HeroSection = () => {
    // DIPERBARUI: Konten slide
    const slides: Slide[] = [
        {
            title: "Welcome to My Portfolio",
            subtitle: "I'm Naisya Yuen, a passionate [Your Role]",
            imageUrl: "/images/banner_01.JPG",
        },
        {
            title: "Creative Solutions",
            subtitle: "Take a look at my recent projects and case studies.",
            imageUrl: "/images/banner_02.JPG",
        },
        {
            title: "Let's Collaborate",
            subtitle: "Have an idea? Let's build something amazing together.",
            imageUrl: "/images/banner_03.jpg",
        },
    ]

    return (
        // Menambahkan id="home" agar link navbar berfungsi
        <Carousel fade interval={4000} data-bs-theme="dark" id="home">
            {slides.map((slide, index) => (
                <Carousel.Item key={index}>
                    <div 
                        style={{
                            backgroundImage:`url(${slide.imageUrl})`,
                            backgroundSize: "cover",
                            backgroundPosition: "center",
                            height: "60vh", // Anda bisa sesuaikan tinggi carousel
                            filter: "brightness(0.7)" // Opsional: meredupkan gambar agar teks jelas
                        }}>
                    </div>
                    {/* DIPERBARUI: Wrapper untuk Caption agar lebih rapi di tengah */}
                    <Carousel.Caption className="d-flex flex-column justify-content-center h-100 text-white">
                        <h1 className="display-4 fw-bold">{slide.title}</h1>
                        <p className="lead">{slide.subtitle}</p>
                    </Carousel.Caption>
                </Carousel.Item>
            )
            )}
        </Carousel>
    )
}

export default HeroSection;