'use client'
import React from "react";
// DIPERBARUI: Sesuaikan nama import
import MainNavbar from "../components/navbar"; 
import HeroSection from "../components/herosection";
import WorkSection from "../components/worksection";
import ServiceSection from "../components/servicesection";
import ContactSection from "../components/contactsection";
 
// Boostrap
import { Container } from "react-bootstrap";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Button from "react-bootstrap/Button";
import Image from "next/image"; // Menggunakan Image dari Next.js
import Footer from "../components/footer";
 
export default function Home(){
  return(
    <div>
      {/* DIPERBARUI: Nama komponen */}
      <MainNavbar/> 
      <main>
        <HeroSection></HeroSection>
        
        {/* DIPERBARUI: Konten "About Me" */}
        <Container className="my-5" id="about">
          <Row className="my-4">
            <Col md={6} className="align-self-center mb-3">
            <h2>About Me</h2>
            <h6>My Journey as a [Your Role, e.g., Developer]</h6>
            <p>
              Lorem ipsum, dolor sit amet consectetur adipisicing elit. Perferendis illo commodi iste, beatae repellendus asperiores voluptas perspiciatis, eius consequatur animi, excepturi porro consectetur nostrum similique quia illum. Enim, quo suscipit.
            </p>
            <Button variant="info">Download CV</Button>
            </Col>
            <Col md={6} className="text-center"> {/* Ditambahkan text-center untuk alignment gambar */}
              <p>
                <Image src="/images/image_04.jpg" alt="About Naisya Yuen" width={500} height={500} style={{ objectFit: 'cover', borderRadius: '10px' }}></Image>
              </p>
            </Col>
          </Row>
        </Container>
        
        {/* DIPERBARUI: Latar belakang gelap dan padding */}
        <section className="bg-dark py-5" id="portfolio">
          <WorkSection></WorkSection>
        </section>
        
        <section id="services">
          <ServiceSection></ServiceSection>
        </section>
        
        {/* DIPERBARUI: Latar belakang dibuat 'light' agar 'Card' kontak terlihat rapi */}
        <section id="contact" className="bg-light py-5">
          <ContactSection></ContactSection>
        </section>
        
        <section>
          <Footer></Footer>
        </section>
      </main>
    </div>
  )
}